#OTM_oferta {
  position: relative;
}

.OTM_oferta_page {
  position: relative;
  font-family: "Lato", sans-serif;
  color: #222222;
}
.OTM_oferta_center_text {
  display: flex;
  align-items: center;
  font-size: 30px;
  justify-content: center;
}
#OTM_oferta_choseperiod {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.OTM_oferta_choose_preiod {
  max-width: 230px;
  margin-top: 7px;
  font-family: "Lato", sans-serif;
  line-height: 1.3;
  display: block;
  border: 1px solid #777777;
  color: #222222;
  padding: 2px;

  border-radius: 5px;
  padding: 5px;
  width: 60%;
  text-align: center;
}
.OTM_oferta_choose_title {
  max-width: 300px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Lato", sans-serif;
  line-height: 1.3;
  display: block;

  color: #222222;
  padding: 2px;

  border-radius: 20vw;
  padding: 5px;
  width: 60%;
  text-align: center;
}

#OTM_oferta_form1 {
  height: 85vh;
  position: relative;
}
