#otm-home-join {
  height: 85vh;
  position: relative;
}

#otm-home-join_div {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#otm_home_down_arrow {
  display: flex;
  position: absolute;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  transform: translate(-50%, -50%);
  left: 50%;
  bottom: 40px;
}

#otm_site__img {
  width: 100%;
}

#otm-home-join_div_center {
  width: 60%;
}
#otm_home-joinus {
  display: flex;
  width: 100%;

  align-items: center;
  transform: translate(-50%, 0%);
  margin-left: 50%;
  margin-bottom: 10%;
  text-align: center;
  flex-direction: column;
}
#otm_home-joinus-button,
#otm_home-joinus-button2 {
  width: 100%;
  padding: 14px 40px;
  margin: 0 2px 0 2px;
  text-decoration: none !important;
  font-family: "Lato", sans-serif !important;
  box-shadow: none;
}
#otm_home-joinus-button2 {
  background-color: transparent;
  color: #8198ae;
  padding: 14px 30px;
  border: 1px solid #c9d9e9;
}
#otm_home_line_buttons {
  justify-content: center;
  display: flex;
}

#OTM_seeinsta {
  width: 60%;
  margin-left: 22%;
  display: flex;

  align-items: center;
}

#OTM_seeinsta_logo {
  float: left;
  width: 30%;
  display: flex;
  justify-content: center;
}
#OTM_seeinsta_text {
  float: right;
  width: 60%;

  font-weight: bold;

  color: #222222;
  font-family: "Lato", sans-serif;
  font-size: 1.8vw;
}

#OTM_home_insta {
  background: linear-gradient(to right, #f7c94c, #e95950);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

#OTM_home_partner {
  background: linear-gradient(to right, #1779d4, #0c45a2);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}
#OTM_contact_midpage {
  width: 80%;
  margin-left: 10%;
  font-family: "Lato", sans-serif;
  color: #222222;
}

#OTM_home_photo_title {
  text-align: center;
  font-weight: bold;
  color: #222222;
  font-family: "Lato", sans-serif;
  font-size: 1.8vw;
  margin-bottom: 2vw;
}

.OTM_home_photos {
  width: 25%;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 2%;
  font-weight: bold;
}
.OTM_home_photos_row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  margin-left: 10%;
}

#OTM_onetiome_gif {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  height: 100vh;
}

.show {
  display: block !important;
}

.hide {
  display: none !important;
}

#OTM_home_photobutton {
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Lato", sans-serif;
  line-height: 1.3;
  display: block;
  border: 1px solid #777777;
  color: #222222;

  border-radius: 20vw;
  padding: 5px 10px 5px 10px;

  text-align: center;
}

@media screen and (max-width: 1200px) {
  #otm-home-join_div_center {
    width: 80%;
  }
  #OTM_home_photobutton {
    font-size: 1.5vw;
  }
}

@media screen and (max-width: 768px) {
  #otm-home-join_div_center {
    width: 100%;
  }

  #OTM_seeinsta {
    width: 96%;
    margin-left: 10%;
  }
  #OTM_seeinsta_text {
    font-size: 3.8vw;
    margin-left: -10%;
    width: 80%;
  }

  #OTM_home_photo_title {
    font-size: 3.8vw;
  }
  #OTM_seeinsta_logo {
    margin-right: 3%;
    margin-left: -10%;
  }
}

@media screen and (max-width: 600px) {
  #otm_site__img {
    content: url("/public/images/onthemap/main_mob.png");
  }

  #OTM_home_photobutton {
    font-size: 2vw;
  }

  #OTM_contact_midpage {
    width: 90%;
    margin-left: 5%;
  }
