.OTM_more_page {
  width: 80%;
  margin-left: 10%;
  position: relative;
  font-family: "Lato", sans-serif;
  color: #222222;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.OTM_more_center_text {
  display: flex;
  align-items: center;
  font-size: 30px;
  justify-content: center;
}
#OTM_more_midpage {
  width: 80%;
  margin-left: 10%;
  font-family: "Lato", sans-serif;
  color: #222222;
}
#OTM_more_discover {
  text-align: center;
}

.OTM_more_qa {
  margin-bottom: 1rem !important;
  padding-right: 8px;
  padding-left: 8px;
  font-family: "Lato", sans-serif;
  padding: 1rem;
  width: 80%;
  max-width: 800px;
  box-shadow:
    0 1px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border: 0 solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}
.OTM_more_qa_row {
  display: flex;
}
.OTM_more_qa_text {
  width: 79%;
  font-weight: bold;
}
.OTM_more_qa_arrow {
  width: 19%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.OTM_more_ans {
  padding: 10px;
  margin-top: 10px;
  color: #888888;
}
.show {
  display: block !important;
}

.hide {
  display: none !important;
}
.rotate0 {
  rotate: 0;
}
.rotate180 {
  rotate: 180deg;
}
@media screen and (max-width: 600px) {
  #OTM_more_midpage {
    width: 90%;
    margin-left: 5%;
  }
  .OTM_more_page {
    width: 90%;
    margin-left: 5%;
  }
}
